.Card {
  text-align: center;
  -webkit-box-shadow: 2px 2px 20px -3px #bfbfbf;
          box-shadow: 2px 2px 20px -3px #bfbfbf;
  border-radius: 5px;
  padding: 1em 0.8em 0.5em 0.8em;
  margin: 1em 0;
  height: 93%;
  background-color: white;
}

.CardName {
  text-transform: capitalize;
  font-size: 1.9em;
  padding-top: 10px;
  font-family: 'Circular Bold';
}

.Logo i {
  font-size: 45px;
}

.CardName:after {
  content: '';
  position: absolute;
  margin-top: 50px;
  left: 50%;
  width: 70px;
  border: 2px solid #ececec;
  border-radius: 4px;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .05);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .05);
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
}

.CardBody {
  color: inherit;
  padding: 1em;
  margin-top: 20px;
}



